
.Burger {
    width: 100%;
    height: 260px;
    overflow: scroll;
    margin: auto;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
    flex-basis: 100%;
}

@media (min-width: 500px) and (min-height: 400px) {
    .Burger {
        width: 350px;
        height: 300px;
    }
}

