.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    color: white;
    background-color: #703B09;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    z-index: 90;
    box-sizing: border-box;
}

@media only screen and (max-width: 700px){
    .desktopOnly{
        display: none;
    }
}